<template>
  <div class="comp withdrawal_card">
    <div class="withdrawal_head">
      <span class="text_uppercase">{{ title }}</span>
      <icon-font
        v-if="iconInfo !== false"
        name="icon-Icon_information"
        :size="loadVariables(isApp ? 'withdrawal_icon_info_size_app' : 'withdrawal_icon_info_size_pc')"
        :dark="loadVariables(isApp ? 'withdrawal_icon_info_color_app' : 'withdrawal_icon_info_color_pc')"
        @click="$emit('view-info')"
      />
    </div>
    <div id="washScheduleId" class="withdrawal_box">
      <div class="lt" @click="$emit('drawal-click', 'withdraw')">
        <div class="lt_img" :style="$loadIcon('user_wallet', true)"></div>
        <div class="lt_account">
          <div class="unit">
            <div>{{ withdrawalText }}</div>
            <!-- <icon-font
              name="icon-Icon_Right"
              :size="loadVariables(isApp ? 'withdrawal_icon_right_size_app' : 'withdrawal_icon_right_size_pc')"
              :dark="loadVariables(isApp ? 'withdrawal_icon_right_color_app' : 'withdrawal_icon_right_color_pc')"
            /> -->
          </div>
          <div v-if="subTitle !== false" class="text break_word">
            {{ $t('UI_MINE_Withdrawal') }}
          </div>
        </div>
      </div>
      <div class="rt">
        <div class="rt_process">
          <div class="process_top">
            <span class="process_text">
              {{ isFormatMillageAndNumber(value.current_bet, true) }}/{{
                isFormatMillageAndNumber(value.all_bet, true)
              }}
            </span>
            <span class="process_tion">{{ processValue }}%</span>
          </div>
          <v-progress-linear
            :color="loadVariables(isApp ? 'withdrawal_progress_color_app' : 'withdrawal_progress_color_pc')"
            :background-color="loadVariables(isApp ? 'withdrawal_progress_bg_app' : 'withdrawal_progress_bg_pc')"
            rounded
            :value="processValue"
          />
          <div class="process_tip break_word">
            {{ $t('UI_MINE_Desbloquear') }}
          </div>
        </div>
        <div class="rt_look">
          <span class="look_text break_word">
            {{ $t('UI_MINE_Desbloquear_money') }}
          </span>
          <span class="look_gold">{{ notwithdrawText }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { isFormatMillageAndNumber } from '@/utils/helper'
import _common from '@style/views/mine.modules.scss'

export default {
  name: 'UserWithdrawal',
  props: {
    iconInfo: {
      type: Boolean,
      default: false
    },
    title: String,
    subTitle: {
      type: Boolean,
      default: true
    },
    value: {
      type: Object,
      default: () => {
        return {
          all_bet: 0,
          can_withdraw: 0,
          current_bet: 0,
          not_withdraw: 0
        }
      }
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol || '' : ''
    },
    withdrawalText() {
      const value = this.value
      return `${this.currencySymbol} ${this.isFormatMillageAndNumber(value && value.can_withdraw ? value.can_withdraw : 0, true)}`
    },
    notwithdrawText() {
      const value = this.value
      return `${this.currencySymbol} ${this.isFormatMillageAndNumber(value && value.not_withdraw ? value.not_withdraw : 0, true)}`
    },
    processValue() {
      const value = this.value
      return !value.current_bet && !value.all_bet ? 0 : parseFloat((value.current_bet / value.all_bet) * 100).toFixed(2)
    }
  },
  methods: {
    loadVariables: (key = '') => _common[key],
    // 千分比转换
    isFormatMillageAndNumber(key, isState) {
      return isFormatMillageAndNumber(key, isState, this.systemConfig?.thousand_mark || 0)
    }
  }
}
</script>

