<template>
  <custom-dialog v-model="$visible">
    <div class="comp avator_box">
      <div class="avator_title">
        <span class="text break_word">{{ $t('UI_PLAYER_USERNAME') }}</span>
        <icon-font
          name="icon-Icon_Cloose"
          :size="loadVariables(isApp ? 'avator_dialog_close_icon_size_app' : 'avator_dialog_close_icon_size_pc')"
          :dark="loadVariables(isApp ? 'avator_dialog_close_icon_color_app' : 'avator_dialog_close_icon_color_pc')"
          @click="$visible = false"
        />
      </div>
      <div class="avator_field">
        <v-text-field
          class="input_radius"
          v-model="userName"
          :height="loadVariables(isApp ? 'avator_field_height_app' : 'avator_field_height_pc')"
          :background-color="loadVariables(isApp ? 'avator_field_bg_app' : 'avator_field_bg_pc')"
          outlined
          hide-details
          single-line
          solo
          flat
        >
          <template slot="prepend-inner">
            <icon-font
              class="prepend-icon"
              name="icon-icon_account_input"
              :size="loadVariables(isApp ? 'avarot_dialog_close_icon_size_app' : 'avarot_dialog_close_icon_size_pc')"
              :dark="loadVariables(isApp ? 'avarot_dialog_close_icon_color_app' : 'avarot_dialog_close_icon_color_pc')"
            />
          </template>
        </v-text-field>
      </div>
      <div class="avator_name break_word">
        {{ $t('UI_Hall_ChangeAvatar') }}
      </div>
      <div class="avator_sprite">
        <div class="avator_sprite_item" v-for="item in userSprite" :key="item.sid" :cols="3">
          <user-sprite-image
            :value="headimg"
            :cur="item.sid"
            :size="loadVariables(isApp ? 'avarot_dialog_user_img_size_app' : 'avarot_dialog_user_img_size_pc')"
            :is-active="true"
            @click.native="headimg = item.sid"
          >
          </user-sprite-image>
        </div>
      </div>
      <div class="avator_btn">
        <div class="btn btn_radius" @click="confirm" v-zoom>
          {{ $t('UI_MINE_salvarBtn') }}
        </div>
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { updateUserInfo } from '@/api/modules/login.js'
import UserSpriteImage from '@/components/ui/userSpriteImage/user-sprite-image'
import userSprite from '@/components/ui/userSpriteImage/userSprite.js'
import _common from '@style/views/mine.modules.scss'

export default {
  components: {
    UserSpriteImage
  },
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      userName: '',
      headimg: 1,
      userSprite
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    $visible: {
      get() {
        return this.value
      },
      set(val) {
        this.$emit('input', val)
      }
    }
  },
  watch: {
    $visible: {
      handler(val) {
        if (!val) return
        this.headimg = this.user.headimg
        this.userName = this.user.nickname
      }
    }
  },
  methods: {
    loadVariables: (key = '') => _common[key],
    async confirm() {
      if (!this.userName) return this.$pop.message().show({ content: this.$t('UI.System_24') })
      if (this.userName.length < 6 || this.userName.length > 16)
        return this.$pop.message().show({ content: this.$t('UI_ACCOUNT_TIP_NICKNAME') })
      const params = {
        token: this.user.token,
        language: this.languageType,
        headimg: `${'0' + this.headimg}`,
        nickname: this.userName
      }
      const { code, data, msg } = await updateUserInfo(params)
      this.$pop.message().show({ content: msg })
      if (code == 0) {
        const { headimg } = data
        const userData = this.user
        userData.nickname = this.userName
        userData.headimg = +headimg
        this.$store.commit('base/SET_USER', userData)
        this.$store.commit('base/SET_USER_HISTORY', userData)
        this.$visible = false
      }
    },
    imgClick(sid) {
      if (this.headimg == sid) return
      this.headimg = sid
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/mine.modules.scss';
.comp.avator_box {
  width: $avarot_dialog_box_width_app;
  padding: $avarot_dialog_box_padding_app;
  border-radius: $avarot_dialog_box_radius_app;
  background: $avarot_dialog_box_bg_app;
  overflow: $avarot_dialog_box_overflow_app;
  .avator_title {
    width: $avarot_dialog_box_title_width_app;
    cursor: pointer;
    @include setBox(row, space-between, center);
    .text {
      font-size: $avarot_dialog_box_title_size_app;
      font-weight: $avarot_dialog_box_title_weight_app;
      color: $avarot_dialog_box_title_color_app;
      line-height: $avarot_dialog_box_title_line_app;
    }
  }
  .avator_field {
    width: $avarot_dialog_box_field_width_app;
    margin-top: $avarot_dialog_box_field_mt_app;
    ::v-deep .v-input__prepend-inner {
      margin-right: $avarot_dialog_box_field_input_mr_app;
    }
  }
  .avator_name {
    width: $avarot_dialog_box_name_width_app;
    margin-top: $avarot_dialog_box_name_mt_app;
    font-size: $avarot_dialog_box_name_size_app;
    font-weight: $avarot_dialog_box_name_weight_app;
    color: $avarot_dialog_box_name_color_app;
    line-height: $avarot_dialog_box_name_line_app;
  }
  .avator_sprite {
    width: $avarot_dialog_box_sprite_widht_app;
    padding: $avarot_dialog_box_sprite_padding_app;
    border-bottom: $avarot_dialog_box_sprite_bb_app;
    @include setBox(row, center, center, wrap);
    .avator_sprite_item {
      width: $avarot_dialog_box_sprite_item_widht_app;
      margin-top: $avarot_dialog_box_sprite_item_mt_app;
      @include setBox(row, center, center);
    }
  }
  .avator_btn {
    width: $avarot_dialog_box_avator_btn_width_app;
    padding: $avarot_dialog_box_avator_btn_padding_app;
    cursor: pointer;
    .btn {
      width: $avarot_dialog_box_avator_btn_child_width_app;
      height: $avarot_dialog_box_avator_btn_child_height_app;
      font-size: $avarot_dialog_box_avator_btn_child_size_app;
      font-weight: $avarot_dialog_box_avator_btn_child_weight_app;
      color: $avarot_dialog_box_avator_btn_child_color_app;
      line-height: $avarot_dialog_box_avator_btn_child_line_app;
      background: $avarot_dialog_box_avator_btn_child_bg_app;
      box-shadow: $avarot_dialog_box_avator_btn_child_shadow_app;
      @include setBox(row, center, center);
      &:hover {
        @include setFilter($btn_hover_filter, $btn_hover_filter_type);
      }
    }
  }
}

@include breakpoint-only-up() {
  .comp.avator_box {
    width: $avarot_dialog_box_width_pc;
    padding: $avarot_dialog_box_padding_pc;
    border-radius: $avarot_dialog_box_radius_pc;
    background: $avarot_dialog_box_bg_pc;
    overflow: $avarot_dialog_box_overflow_pc;
    user-select: none;
    .avator_title {
      width: $avarot_dialog_box_title_width_pc;
      cursor: pointer;
      @include setBox(row, space-between, center);
      .text {
        font-size: $avarot_dialog_box_title_size_pc;
        font-weight: $avarot_dialog_box_title_weight_pc;
        color: $avarot_dialog_box_title_color_pc;
        line-height: $avarot_dialog_box_title_line_pc;
      }
    }
    .avator_field {
      width: $avarot_dialog_box_field_width_pc;
      margin-top: $avarot_dialog_box_field_mt_pc;
      ::v-deep .v-input__prepend-inner {
        margin-right: $avarot_dialog_box_field_input_mr_pc;
      }
    }
    .avator_name {
      width: $avarot_dialog_box_name_width_pc;
      margin-top: $avarot_dialog_box_name_mt_pc;
      font-size: $avarot_dialog_box_name_size_pc;
      font-weight: $avarot_dialog_box_name_weight_pc;
      color: $avarot_dialog_box_name_color_pc;
      line-height: $avarot_dialog_box_name_line_pc;
    }
    .avator_sprite {
      width: $avarot_dialog_box_sprite_widht_pc;
      padding: $avarot_dialog_box_sprite_padding_pc;
      border-bottom: $avarot_dialog_box_sprite_bb_pc;
      @include setBox(row, center, center, wrap);
      .avator_sprite_item {
        width: $avarot_dialog_box_sprite_item_widht_pc;
        margin-top: $avarot_dialog_box_sprite_item_mt_pc;
        @include setBox(row, center, center);
      }
    }
    .avator_btn {
      width: $avarot_dialog_box_avator_btn_width_pc;
      padding: $avarot_dialog_box_avator_btn_padding_pc;
      cursor: pointer;
      .btn {
        width: $avarot_dialog_box_avator_btn_child_width_pc;
        height: $avarot_dialog_box_avator_btn_child_height_pc;
        font-size: $avarot_dialog_box_avator_btn_child_size_pc;
        font-weight: $avarot_dialog_box_avator_btn_child_weight_pc;
        color: $avarot_dialog_box_avator_btn_child_color_pc;
        line-height: $avarot_dialog_box_avator_btn_child_line_pc;
        background: $avarot_dialog_box_avator_btn_child_bg_pc;
        box-shadow: $avarot_dialog_box_avator_btn_child_shadow_pc;
        @include setBox(row, center, center);
        &:hover {
          @include setFilter($btn_hover_filter, $btn_hover_filter_type);
        }
      }
    }
  }
}
</style>
