<template>
  <v-container class="page mine">
    <user-head :user="user" @update="avatorVisible = true" />
    <div class="mine_box">
      <div class="mine_tabs">
        <div class="tabs">
          <btn-tab :width="300" radius="18px" :tag-list="tabList" :type="tabIndex" @changeTab="changeTab" />
        </div>
      </div>
      <template v-if="tabIndex == 0">
        <div class="mine_account">
          <div class="mine_account_money">
            <span class="mine_account_tip">
              {{ $t('UI_Balance') }}
            </span>
            <span class="mine_account_gold">
              {{ user.gold ? `${currencySymbol} ${isFormatMillageAndNumber(user.gold, true)}` : 0 }}
            </span>
          </div>
          <div class="mine_account_btn" @click="showAccount('deposit')">
            {{ $t('UI.MakeMoney_4_Promotion_17') }}
          </div>
        </div>
        <user-withdrawal
          :title="$t('UI_withdrawal')"
          icon-info
          :value="withdrawData"
          @view-info="viewRatio"
          @drawal-click="showAccount"
        />
        <div class="mine_account_action">
          <span class="action_text break_word" @click="historyVisible = true">
            {{ $t('UI_Registros_de_Deposito') }}
          </span>
        </div>
      </template>
      <template v-else>
        <user-vip :vip-list="vipList" :is-kilobit="isKilobit" />
        <template v-if="isBetShow">
          <user-bet :list="betData" :vip-list="vipList" />
        </template>
      </template>
    </div>
    <!-- 游戏提现打码率 弹窗 -->
    <game-ratio-dialog v-model="ratioVisible" @close="ratioVisible = false" />
    <avator-dialog v-model="avatorVisible" :user="user" />

    <!-- 提现记录弹窗 -->
    <custom-dialog v-model="historyVisible" :full="true">
      <div :style="{ width: isApp ? '100%' : '650px' }">
        <withdrawal-recharge-history :tab="3" @close="closeHistory" />
      </div>
    </custom-dialog>
  </v-container>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import UserHead from './ui/user-head'
import UserWithdrawal from './ui/withdrawal'
import UserVip from './ui/vip'
import UserBet from './ui/bet'
import AvatorDialog from './ui/avator-dialog'
import GameRatioDialog from './ui/game-ratio-dialog'
import withdrawalRechargeHistory from '@/components/ui/withdrawal-recharge-history'
import { PLATFORM_VALUE } from '@/api/setting.js'
import { getUserInfoAndOrder } from '@/api/modules/config.js'
import { info, newPercentList } from '@/api/modules/bet'
import { vipList as getMineVipList } from '@/api/modules/bet.js'
import { isFormatMillageAndNumber } from '@/utils/helper'
import btnTab from '@/components/ui/btn-tab/index.vue'
import ContainerMixin from '@/components/containerMixin'

export default {
  name: 'UserIndex',
  components: {
    btnTab,
    UserHead,
    UserWithdrawal,
    UserVip,
    UserBet,
    AvatorDialog,
    GameRatioDialog,
    withdrawalRechargeHistory
  },
  mixins: [ContainerMixin],
  props: {
    defaultIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      // 个人信息数据请求标志
      isCatch: false,
      tabIndex: 0,
      tabList: [
        { name: this.$t('UI_Carteira'), isCatch: false, value: 0 },
        { name: this.$t('UI_LoginAccount'), isCatch: false, value: 1 }
      ],
      avatorVisible: false,
      ratioVisible: false,
      historyVisible: false,
      // 钱包初始数据
      withdrawData: {
        all_bet: 0,
        can_withdraw: 0,
        current_bet: 0,
        not_withdraw: 0
      },
      betData: [],
      vipList: [], // vip列表
      isKilobit: false // vip千分位开关
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    ...mapGetters('base', ['isBetShow']),
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    }
  },
  watch: {
    defaultIndex: {
      immediate: true,
      handler: function (val) {
        this.tabIndex = val
        this.changeTab({ i: val })
      }
    }
  },
  methods: {
    async changeTab(tab) {
      const index = tab.i
      if (!this.tabList[index].isCatch) {
        const initActionMap = {
          0: this.initWithDrawData,
          1: this.initPersonData
        }
        const initAction = initActionMap[index]
        await initAction()
        this.tabList[index].isCatch = true
      }
      this.tabIndex = index
    },
    viewRatio() {
      this.ratioVisible = true
    },
    /**
     * 标识
     * @param tab deposit：充值，withdraw：提现
     */
    showAccount(tab = 'deposit') {
      this.$store.dispatch('modal/setDepositWithdrawShow', { tab })
    },
    // 获取钱包数据
    async initWithDrawData() {
      this.$pop.loading().show()
      const params = {
        type: 0,
        token: this.user.token,
        language: this.languageType
      }
      const { code, data = {} } = await getUserInfoAndOrder(params)
      if (code == 0) {
        const { user_info = {} } = data
        this.withdrawData = user_info
      }
      this.$pop.loading().hide()
    },
    async initPersonData() {
      this.$pop.loading().show()
      await this.getVipList()
      await this.initBetData()
      this.$pop.loading().hide()
    },
    // 请求vip数据
    async getVipList() {
      const par = {
        token: this.user.token,
        language: this.languageType
      }

      const res = await getMineVipList(par).catch(e => e)
      const { code, data } = res
      if (code != 0) return

      // vip_bets          pc端 当前打码量
      // max_recharge      pc端 最大充值数
      // recharge_gold     pc端 当前充值数
      // vip_id            当前等级对应的列表 id
      // vip_faq_name      根据这个字段获取VIP保级降级功能说明
      // lv_keep_recharge  开启保级后，当前等级要充值多少钱才能解开保级 ------- 保级使用
      // week_recharge     本周已经充值了多少钱 ------------------------------ 保级使用
      // thousand_mark 	 vip千分位开关 0关 1开
      const list = data.list && Array.isArray(data.list) ? data.list : []
      const vipListOld = data.show_vip_list && Array.isArray(data.show_vip_list) ? data.show_vip_list : []
      // 合并两个列表的数据
      const vipListNew = vipListOld.map(item => {
        const vipItem = list.find(f => f.id == item.id)
        const newItem = { ...vipItem, ...item }
        return {
          ...newItem,
          recharge_gold: data.recharge_gold, // 当前充值数
          vip_bets: data.vip_bets, // 当前打码
          week_recharge: data.week_recharge // 当前打码
        }
      })

      this.vipList = vipListNew
      this.isKilobit = data.thousand_mark == 1
    },
    // 初始化洗码数据
    async initBetData() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }
      const par = {
        token: this.user.token,
        language: this.languageType,
        platform: PLATFORM_VALUE
      }
      const [resInfo, resPrecent] = await Promise.all([
        info(par).catch(e => e), // 获取洗码领取信息
        newPercentList(params).catch(e => e) // 获取洗码表格信息
      ])
      if (!resInfo.code == 0 && !resPrecent.code == 0) return
      const infoList = resInfo?.data?.list || []
      const precentList = resPrecent?.data?.list || []
      this.betData = infoList
        .sort((a, b) => a.sort - b.sort)
        .map(item => {
          const { name, type_id } = item
          const precentData = precentList[type_id] || []
          return {
            name,
            data: precentData,
            id: type_id
          }
        })
    },
    closeHistory() {
      this.historyVisible = false
    },
    // 千分比转换
    isFormatMillageAndNumber(key, isState) {
      return isFormatMillageAndNumber(key, isState, this.systemConfig?.thousand_mark || 0)
    }
  }
}
</script>

