<template>
  <div class="comp vip_card">
    <div class="vip_card_head">
      {{ vipLevel }}
    </div>
    <div class="vip_card_box">
      <div class="lt">
        <div class="user_vip" :style="$loadIcon('user_vip', true)"></div>
      </div>
      <div class="rt">
        <template v-if="endLevel">
          <div class="highest_text">{{ $t('UI_VIP_highest') }}</div>
        </template>
        <template v-if="!endLevel && processData">
          <div class="vip_process">
            <div class="process_deposit">
              <div class="process_text">
                <div class="process_lt">
                  <icon-font
                    name="icon-icon_account_recharge"
                    :size="loadVariables(isApp ? 'vip_process_icon_size_app' : 'vip_process_icon_size_pc')"
                  />
                  <span class="lt_text break_word">
                    {{ $t('UI_VIP_Vip.deposit') }}
                  </span>
                </div>
                <div class="process_rt">
                  {{ $u.formatKilobit(processData.recharge_gold, isKilobit) }}
                  /
                  {{ $u.formatKilobit(processData.min_recharge, isKilobit) }}
                </div>
              </div>
              <v-progress-linear
                :color="loadVariables(isApp ? 'vip_deposit_process_color_app' : 'vip_deposit_process_color_pc')"
                :background-color="loadVariables(isApp ? 'vip_deposit_process_bg_app' : 'vip_deposit_process_bg_pc')"
                :value="(processData.recharge_gold / processData.min_recharge) * 100"
                rounded
              />
            </div>
            <div class="process_bet">
              <div class="process_text">
                <div class="process_lt">
                  <icon-font
                    name="icon-a-icon_account_betamount"
                    :size="loadVariables(isApp ? 'vip_process_icon_size_app' : 'vip_process_icon_size_pc')"
                  />
                  <span class="lt_text break_word">
                    {{ $t('UI_VIP_Vip.bet') }}
                  </span>
                </div>
                <div class="process_rt">
                  {{ $u.formatKilobit(processData.vip_bets, isKilobit) }}
                  /
                  {{ $u.formatKilobit(processData.min_bet, isKilobit) }}
                </div>
              </div>
              <v-progress-linear
                :color="loadVariables(isApp ? 'vip_bet_process_color_app' : 'vip_bet_process_color_pc')"
                :background-color="loadVariables(isApp ? 'vip_bet_process_bg_app' : 'vip_bet_process_bg_pc')"
                :value="(processData.vip_bets / processData.min_bet) * 100"
                rounded
              />
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import _common from '@style/views/mine.modules.scss'

export default {
  props: {
    vipList: {
      type: Array,
      default() {
        return []
      }
    },
    // vip千分位开关
    isKilobit: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['user', 'isApp']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    // 当前的vip等级
    vipLevel() {
      const lv = this.$t('UI_VIP_Vip.lv')
      return lv + this.getVipCurrentLevel()
    },
    vipIndex() {
      const { vip_id } = this.user
      const fIndex = this.vipList.findIndex(f => f.id == vip_id)
      return fIndex
    },
    // 最高等级
    endLevel() {
      return this.vipIndex === this.vipList.length - 1
    },
    processData() {
      return this.vipList[this.vipIndex + 1] || {}
    },
    processValue() {}
  },
  methods: {
    loadVariables: (key = '') => _common[key],
    getVIpProcessData() {
      // 获取当前下一级vip数据
      const { vip_id } = this.user
      const fIndex = this.vipList.findIndex(f => f.id == vip_id)
      if (fIndex === this.vipList.length) {
        this.is = 1
      }
      return this.vipList[fIndex + 1]
    }
  }
}
</script>

