              line-height: $vip_card_box_rt_vip_process_text_rt_app_line;
              text-align: $vip_card_box_rt_vip_process_text_rt_app_align;
            }
          }
        }
        .process_bet {
          margin: $vip_card_box_rt_vip_process_bet_app_margin;
        }
      }
    }
  }
}
