<template>
  <custom-dialog v-model="show">
    <div class="comp game_dialog">
      <div class="head_title">
        <div class="text text_1">{{ $t('General.General_7') }}</div>
        <div class="close" @click="show = false">
          <icon-font name="icon-Icon_Cloose" size="18" />
        </div>
      </div>
      <div class="game_ratio_body">
        <div class="game_ratio_box">
          <div class="drawal_head">
            <span class="text_lt">
              {{ $t('UI.MakeMoney_3_MyCommission_10') }}
            </span>
            <span class="text_rt">
              {{ $t('UI_MINE_coeficiente') }}
            </span>
          </div>
          <div class="drawal_box">
            <template v-if="list.length">
              <div v-for="item in list" :key="item.id" class="drawal_item">
                <span class="lt">{{ item.name }}</span>
                <span class="rt">{{ item.ratio }}</span>
              </div>
            </template>
            <template v-else>
              <empty />
            </template>
          </div>
        </div>
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { getGameRatios } from '@/api/modules/config'
import Empty from '@/components/ui/empty'

export default {
  components: {
    Empty
  },
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    // 是否显示
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      list: []
    }
  },
  computed: {
    ...mapState('base', ['languageType', 'user', 'isApp']),
    // 打开关闭
    show: {
      get() {
        this.getList()

        return this.visible
      },
      set(val) {
        this.$emit('change', val)
      }
    }
  },
  methods: {
    // 获取数据
    async getList() {
      let state = true
      const params = { token: this.user.token, language: this.languageType }

      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 1 * 1000)
      const res = await getGameRatios(params).catch(e => e)
      state = false
      this.$pop.loading().hide()
      if (this.timer) clearTimeout(this.timer)
      if (!res || res.code != 0) return this.$pop.message().show({ content: res && res.msg })

      this.list = res.data && Array.isArray(res.data) ? res.data : []
    }
  },
  beforeDestroy() {
    this.$pop.loading().hide()
    if (this.timer) clearTimeout(this.timer)
  }
}
</script>

