<template>
  <div class="comp user_head">
    <div class="user_box">
      <div class="lt">
        <div class="lt_avator">
          <user-sprite-image
            :value="avator"
            :size="loadVariables(isApp ? 'user_head_avator_size_app' : 'user_head_avator_size_pc')"
            @click.native="$emit('update')"
          />
        </div>
        <div class="lt_user">
          <div class="user_name" @click="$emit('update')">
            {{ user.nickname }}
          </div>
          <div class="user_info">
            <span class="user_id">ID: {{ user.uid }}</span>
            <icon-font
              class="cursor_point"
              name="icon-icon_account_copy"
              :size="loadVariables(isApp ? 'user_head_icon_copy_size_app' : 'user_head_icon_copy_size_pc')"
              @click="onCopy"
            />
          </div>
        </div>
      </div>
      <div class="rt">
        <div class="line">
          <icon-font
            class="icon"
            name="icon-icon_account_phone"
            :size="loadVariables(isApp ? 'user_head_icon_phone_size_app' : 'user_head_icon_phone_size_pc')"
          />
          <span v-if="accountPhone" class="text">{{ accountPhone }}</span>
          <span v-else class="text_error">{{ capitalizeFirstLetter($t('UI_Account_unbound')) }}</span>
        </div>
        <div class="line">
          <icon-font
            class="icon"
            name="icon-icon_account_email"
            :size="loadVariables(isApp ? 'user_head_icon_phone_email_app' : 'user_head_icon_phone_email_pc')"
          />
          <span v-if="user.account" class="text">{{ user.account }}</span>
          <span v-else class="text_error">{{ capitalizeFirstLetter($t('UI_Account_unbound')) }}</span>
        </div>
        <template v-if="isBirthday">
          <div class="line">
            <icon-font
              class="icon"
              name="icon-icon_account_birthday"
              :size="loadVariables(isApp ? 'user_head_icon_birthday_size_app' : 'user_head_icon_birthday_size_pc')"
            />
            <span class="text" :class="{ text_error: !user.birthday }">{{ accountBirthday }}</span>
          </div>
        </template>
      </div>
      <span class="user_action" @click="$router.push({ name: 'UserAccount' })">
        <icon-font
          name="icon-Icon_Right"
          :size="loadVariables(isApp ? 'user_head_icon_right_size_app' : 'user_head_icon_right_size_pc')"
          :dark="loadVariables(isApp ? 'user_head_icon_right_color_app' : 'user_head_icon_right_color_pc')"
        />
      </span>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import UserSpriteImage from '@/components/ui/userSpriteImage/user-sprite-image'
import copy from 'copy-to-clipboard'
import _common from '@style/views/mine.modules.scss'
import { capitalizeFirstLetter } from '@/utils/helper.js'

export default {
  name: 'UserHead',
  components: {
    UserSpriteImage
  },
  props: {
    user: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig']),
    avator() {
      return this.user.headimg || '1'
    },
    accountPhone() {
      const { area, tel } = this.user
      if (!tel) return ''
      return `+${area} ${tel}`
    },
    // 是否开启生日开关 0关 1开
    isBirthday() {
      const { birthday_switch = 0 } = this.systemConfig

      return birthday_switch == 1
    },
    // 生日
    accountBirthday() {
      const { birthday = 0 } = this.user
      if (!birthday) return this.capitalizeFirstLetter(this.$t('UI_Not_filled'))

      return this.$u.datetime.getDate__useTimestamp(birthday * 1000)
    }
  },
  methods: {
    capitalizeFirstLetter,
    loadVariables: (key = '') => _common[key],
    // copy
    onCopy() {
      const isCopy = copy(this.user.uid)
      if (isCopy) this.$pop.message().show({ content: this.$t('UI.System_16') })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/mine.modules.scss';
.comp.user_head {
  width: $user_head_app_width;
  border-bottom: $user_head_app_bb;
  .user_box {
    width: $user_head_user_box_app_width;
    padding: $user_head_user_box_app_padding;
    position: $user_head_user_box_app_position;
    cursor: $user_head_user_box_app_cursor;
    @include setBox(row, center, center);
    .lt {
      flex: $user_head_user_box_lt_app_flex;
      border-right: $user_head_user_box_lt_app_br;
      @include setBox(column, center, center);
      .lt_avator {
        margin: $user_head_user_box_lt_avator_app_margin;
        border-radius: $user_head_user_box_lt_avator_app_radius;
        border: $user_head_user_box_lt_avator_app_border;
      }
      .lt_user {
        width: $user_head_user_box_lt_user_app_width;
        @include setBox(column, center, center);
        .user_name {
          font-size: $user_head_user_box_lt_user_name_app_size;
          font-weight: $user_head_user_box_lt_user_name_app_weight;
          color: $user_head_user_box_lt_user_name_app_color;
          line-height: $user_head_user_box_lt_user_name_app_line;
        }
        .user_info {
          margin-top: $user_head_user_box_lt_user_info_app_mt;
          font-size: $user_head_user_box_lt_user_info_app_size;
          font-weight: $user_head_user_box_lt_user_info_app_weight;
          color: $user_head_user_box_lt_user_info_app_color;
          line-height: $user_head_user_box_lt_user_info_app_line;
          @include setBox(row, center, center);
          .user_id {
            margin-right: $user_head_user_box_lt_user_info_id_app_mr;
          }
        }
      }
    }
    .rt {
      flex: $user_head_user_box_rt_app_flex;
      padding-left: $user_head_user_box_rt_app_pl;
      @include setBox(column, center, flex-start);
      .line {
        width: $user_head_user_box_rt_line_app_width;
        margin-top: $user_head_user_box_rt_line_app_mt;
        @include setBox(row, flex-start, center);
        &:first-child {
          margin-top: $user_head_user_box_rt_line_child_app_mt;
        }
        .icon {
          margin-right: $user_head_user_box_rt_line_icon_app_mr;
        }
        .text {
          font-size: $user_head_user_box_rt_line_text_app_size;
          font-weight: $user_head_user_box_rt_line_text_app_weight;
          color: $user_head_user_box_rt_line_text_app_color;
        }
        .text_error {
          font-size: $user_head_user_box_rt_line_text_err_app_size;
          font-weight: $user_head_user_box_rt_line_text_err_app_weight;
          color: $user_head_user_box_rt_line_text_err_app_color;
        }
      }
    }
    .user_action {
      position: $user_head_user_box_user_action_app_position;
      right: $user_head_user_box_user_action_app_right;
      top: $user_head_user_box_user_action_app_top;
      transform: $user_head_user_box_user_action_app_transform;
      cursor: $user_head_user_box_user_action_app_cursor;
    }
  }
}

@include breakpoint-only-up() {
  .comp.user_head {
    width: $user_head_pc_width;
    margin: $user_head_pc_m;
    border-radius: $user_head_pc_radius;
    background: $user_head_pc_background;
    border-bottom: $user_head_pc_bb;
    .user_box {
      width: $user_head_user_box_pc_width;
      padding: $user_head_user_box_pc_padding;
      position: $user_head_user_box_pc_position;
      cursor: $user_head_user_box_pc_cursor;
      @include setBox(row, center, center);
      .lt {
        flex: $user_head_user_box_lt_pc_flex;
        flex-shrink: $user_head_user_box_lt_pc_shrink;
        flex-grow: $user_head_user_box_lt_pc_grow;
        width: $user_head_user_box_lt_pc_width;
        border-right: $user_head_user_box_lt_pc_br;
        @include setBox(row, center, center);
        .lt_avator {
          margin: $user_head_user_box_lt_avator_pc_margin;
          border-radius: $user_head_user_box_lt_avator_pc_radius;
          border: $user_head_user_box_lt_avator_pc_border;
        }
        .lt_user {
          width: $user_head_user_box_lt_user_pc_width;
          @include setBox(column, center, flex-start);
          .user_name {
            font-size: $user_head_user_box_lt_user_name_pc_size;
            font-weight: $user_head_user_box_lt_user_name_pc_weight;
            color: $user_head_user_box_lt_user_name_pc_color;
            line-height: $user_head_user_box_lt_user_name_pc_line;
          }
          .user_info {
            margin-top: $user_head_user_box_lt_user_info_pc_mt;
            font-size: $user_head_user_box_lt_user_info_pc_size;
            font-weight: $user_head_user_box_lt_user_info_pc_weight;
            color: $user_head_user_box_lt_user_info_pc_color;
            line-height: $user_head_user_box_lt_user_info_pc_line;
            @include setBox(row, center, center);
            .user_id {
              margin-right: $user_head_user_box_lt_user_info_id_pc_margin;
            }
          }
        }
      }
      .rt {
        flex-shrink: $user_head_user_box_rt_pc_shrink;
        flex-grow: $user_head_user_box_rt_pc_grow;
        padding-left: $user_head_user_box_rt_pc_pl;
        @include setBox(column, center, flex-start);
        .line {
          width: $user_head_user_box_rt_line_pc_width;
          margin: $user_head_user_box_rt_line_pc_margin;
          @include setBox(row, flex-start, center);
          &:first-child {
            margin: $user_head_user_box_rt_line_child_pc_margin;
          }
          .icon {
            margin-right: $user_head_user_box_rt_line_icon_pc_mr;
          }
          .text {
            font-size: $user_head_user_box_rt_line_text_pc_size;
            font-weight: $user_head_user_box_rt_line_text_pc_weight;
            color: $user_head_user_box_rt_line_text_pc_color;
          }
          .text_error {
            font-size: $user_head_user_box_rt_line_text_err_pc_size;
            font-weight: $user_head_user_box_rt_line_text_err_pc_weight;
            color: $user_head_user_box_rt_line_text_err_pc_color;
          }
        }
      }
      .user_action {
        position: $user_head_user_box_user_action_pc_position;
        right: $user_head_user_box_user_action_pc_right;
        top: $user_head_user_box_user_action_pc_top;
        transform: $user_head_user_box_user_action_pc_transform;
        cursor: $user_head_user_box_user_action_pc_cursor;
      }
    }
  }
}
</style>
