<template>
  <div class="comp bet_card">
    <div class="bet_card_head">
      {{ $t('UI_Code_WashCodeRatio') }}
    </div>
    <div class="bet_card_box">
      <div class="lt">
        <div class="lt_img" :style="$loadIcon('user_bet', true)"></div>
      </div>
      <div class="rt" :class="{ sigle: !nextIndex }">
        <div class="rt_head">
          <div class="rt_head_level">
            <span class="level_tip">
              {{ $t('UI_Current_level', { X: getVipLevelText(vipIndex) }) }}
            </span>
          </div>
          <template v-if="nextIndex">
            <div class="rt_head_level next_level">
              <span id="head_level" class="level_tip">
                {{ $t('UI_Next_level', { X: getVipLevelText(nextIndex) }) }}
              </span>
            </div>
          </template>
        </div>
        <div v-for="item in list" :key="item.id" class="rt_row">
          <div class="name">{{ item.name }}</div>
          <div class="row_info">
            <div class="rtion">{{ formatPercentText(item.data[vipIndex]) }}</div>
            <template v-if="nextIndex">
              <div class="arrow_box">
                <span class="arrow_one">
                  <icon-font
                    name="icon-icon_account_3arrow"
                    :size="loadVariables(isApp ? 'bet_arror_right_size_app' : 'bet_arror_right_size_pc')"
                  />
                </span>
                <span class="arrow_two">
                  <icon-font
                    name="icon-icon_account_3arrow"
                    :size="loadVariables(isApp ? 'bet_arror_right_size_app' : 'bet_arror_right_size_pc')"
                  />
                </span>
                <span class="arrow_two">
                  <icon-font
                    name="icon-icon_account_3arrow"
                    :size="loadVariables(isApp ? 'bet_arror_right_size_app' : 'bet_arror_right_size_pc')"
                  />
                </span>
              </div>
              <div class="next_rtion">
                <div class="next_rtion_inner" :style="{ '--cellWidth': cellWidth }">
                  {{ formatPercentText(item.data[nextIndex]) }}
                </div>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import _common from '@style/views/mine.modules.scss'

export default {
  props: {
    list: {
      type: Array,
      default: () => []
    },
    vipList: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      cellWidth: '52px'
    }
  },
  computed: {
    ...mapState('base', ['user', 'isApp']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    isAlive() {
      //  is_keep_level 0降级 1未降级
      return this.user.is_keep_level == 1
    },
    // 当前等级
    vipIndex() {
      // 降级状态返回最初等级
      if (!this.isAlive) return 0
      const { vip_id } = this.user
      const fIndex = this.vipList.findIndex(f => f.id == vip_id)
      return fIndex
    },
    // 下一等级
    nextIndex() {
      const { vip_id } = this.user
      const fIndex = this.vipList.findIndex(f => f.id == vip_id)
      // 降级状态返回用户当前的index
      if (!this.isAlive) {
        return fIndex
      }
      // 返回null时表示最高级
      const nextVip = this.vipList[fIndex + 1]
      return nextVip ? fIndex + 1 : null
    }
  },
  watch: {
    nextIndex: {
      handler(val) {
        val && this.$nextTick(this.getCellWidth)
      },
      immediate: true
    }
  },
  methods: {
    loadVariables: (key = '') => _common[key],
    formatPercentText(data) {
      if (!data) return '--'
      const val = data.percent * 100 || 0
      return (val || 0).toFixed(2) + '%'
    },
    getVipLevelText(index) {
      const vipid = this.vipList[index]?.id
      return vipid ? this.getVipCurrentLevel(vipid) : ''
    },
    getCellWidth() {
      const dom = document.querySelector('#head_level')
      if (dom) {
        this.cellWidth = `${dom.offsetWidth}px`
      }
    }
  }
}
</script>

<style lang="scss" scoped>
