            line-height: $bet_card_box_rt_row_next_line_app;
            text-align: $bet_card_box_rt_row_next_text_align_app;
            .next_rtion_inner {
              display: inline-block;
              width: var(--cellWidth);
              text-align: left;
            }
          }
        }
      }
    }
  }
}

